.contactus-main-body{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
    cursor:default;
    margin: 20px;
    color:rgb(9, 9, 78);
}

.contactus-part-1{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap-reverse;
}

.contact-content{
    margin: 30px;
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}

.contact-img{
    width:35%
}

.contact-img img{
    width: 100%;
    border-radius: 20px;
    box-shadow: 18px 15px 6px rgb(9, 9, 78) ;
}

.contact-heading{
    font-size: 25px;
    font-weight: bold;

}

.contact-content-part-1{
    font-size: 18px;
    margin-top: 10px;
}

.contact-content-part-2{
    font-size: 18px;
}

.contactus-part-2{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width:100%;
}

.contactus-part-2 div{
    width:80%;
    margin: 10px;
}

.contact-btn{
    display: flex;
    width: 70%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contact-btn button{
    font-size: 25px;
    font-weight: bold;
    padding: 15px;
    cursor: pointer;
    border: none;
    color: white;
    background-color:rgb(9, 9, 78) ;
}

.contact-btn button:hover{
    box-shadow: 6px 6px 6px gray;
}